import React, { Fragment, useEffect } from "react";
import mernstac1 from "../../styles/assets/images/mernstack/mernstac1.jpg";
import mernstac2 from "../../styles/assets/images/mernstack/mernstac2.jpg";
import mernstac3 from "../../styles/assets/images/mernstack/mernstac3.jpg";
import { useState } from "react";

const MernDevelopmentServices = () => {
  let currentIndex = 0;
  let isPaused = false;
  const [idx, setIdx] = useState("");

  function showSlide(index) {
    const carouselItems = document.querySelectorAll(".carousel-item");
    if (index < 0) {
      index = carouselItems.length - 1;
    } else if (index >= carouselItems.length) {
      index = 0;
    }
    setIdx(index);
    currentIndex = index;
  }

  function autoSlide() {
    showSlide(currentIndex + 1);
  }

  useEffect(() => {
    const carousel = document.querySelector(".carousel");
    carousel?.addEventListener("mouseover", () => {
      isPaused = true;
    });

    carousel?.addEventListener("mouseout", () => {
      isPaused = false;
    });

    setInterval(autoSlide, 3000);
  }, []);

  return (
    <Fragment>
      <section className='section-webdev'>
        <div className='container'>
          <h3 className='heading-tertiary'>
            MERN/MEAN STACK DEVELOPMENT SERVICES
          </h3>
          <p className='description'>
            At our company, we specialize in providing top-notch MERN/MEAN stack
            development services. Our team of experienced developers is
            well-versed in the latest technologies and tools to build scalable
            and high-performance web applications that meet your specific
            business needs.
          </p>
        </div>
      </section>

      <section className='section-blog'>
        <div className='container'>
          <h3 className='heading-tertiary'>
            Building Scalable and High-Performance Web Applications
          </h3>
          <div className='container'>
            {/* <ion-icon
              className='slider'
              name='arrow-back-circle-outline'></ion-icon>
            <ion-icon
              className='slider'
              name='arrow-forward-circle-outline'></ion-icon> */}
            <div className='carousel-container'>
              <div
                className='carousel'
                style={{
                  transform: `translateX(-${idx * 100}%)`,
                }}>
                <div className='carousel-item'>
                  <div className='grid grid--2-cols'>
                    <div className='content-box'>
                      <p className='description'>
                        <strong>
                          At our company, we specialize in providing top-notch
                          MERN/MEAN stack development services. Our team of
                          experienced developers is well-versed in the latest
                          technologies and tools to build scalable and
                          high-performance web applications that meet your
                          specific business needs.
                        </strong>
                      </p>
                    </div>
                    <div className='img-slide-box'>
                      <img
                        className='web-img'
                        src={mernstac1}
                        alt='fullstack devlopment example'
                      />
                    </div>
                  </div>
                </div>
                <div className='carousel-item'>
                  <div className='grid grid--2-cols'>
                    <div className='content-box'>
                      <p className='description'>
                        <strong>
                          Our MERN stack development services include the use of
                          MongoDB, Express.js, React, and Node.js, while our
                          MEAN stack development services include the use of
                          MongoDB, Express.js, AngularJS, and Node.js. Both
                          stacks are built using JavaScript, making them highly
                          efficient and easy to maintain.
                        </strong>
                      </p>
                    </div>
                    <div className='img-slide-box'>
                      <img
                        className='web-img'
                        src={mernstac2}
                        alt='fullstack devlopment example'
                      />
                    </div>
                  </div>
                </div>
                <div className='carousel-item'>
                  <div className='grid grid--2-cols'>
                    <div className='content-box'>
                      <p className='description'>
                        <strong>
                          We understand the importance of building web
                          applications that are responsive, user-friendly, and
                          optimized for search engines. That's why we use
                          popular SEO keywords to ensure that your website ranks
                          well on search engines, driving more traffic and
                          conversions.
                        </strong>
                      </p>
                    </div>
                    <div className='img-slide-box'>
                      <img
                        className='web-img'
                        src={mernstac3}
                        alt='fullstack devlopment example'
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section>
        <div className='container'>
          <h4 className='title'>Conclusion</h4>
          <p className='description'>
            So why wait? Contact us today to learn more about our POC and open
            source development services, and to see how we can help you unleash
            the full power of innovation and flexibility in your next project.
          </p>
        </div>
      </section>
    </Fragment>
  );
};

export default MernDevelopmentServices;
